import React from 'react';

export default (props) => {
  return (
    <li>
      <div class='itinerary-day'>
        <span style={{ fontSize: '17px' }}>Ngy {props.location.day}</span>
      </div>

      <h6>im n: {props.location.title}</h6>
      <div
        dangerouslySetInnerHTML={{
          __html: props.location.description,
        }}
      />

      <ul class='itinerary-meta list-inline-block text-primary'>
        <li>
          <i class='far fa-clock'></i> Thi gian chuyn i: 8am - 4.30pm
        </li>
      </ul>
    </li>
  );
};
